  constructor() {}

  public getStates(): State[] {
    return this.states;
  }

  public getStatesAsOptions(): Option[] {
    return _(_.cloneDeep(this.getStates()))
      .map((state: State): Option => {
        return <Option>{
          label: state.name,
          value: state.abbreviation
        };
      })
      .value();
  }

  public getCountries(): Country[] {
    return _(this.countries)
      .orderBy('countryName')
      .cloneDeep()
      .value();
  }

  public getStatesProvinces(country: string = 'usa'): Option[] {
    let data: Option[] = [];
    country = _.toLower(_.trim(country));

    if (
      country === 'us' ||
      country === 'usa' ||
      country === 'america' ||
      /^united\s+states/.test(country)
    ) {
      data = _.cloneDeep(this.getStatesAsOptions());
    } else if (country === 'canada') {
      data = _.cloneDeep(this.getStatesAsOptions());
    }

    return data;
  }
